/**
 * 获取 Lucide 图标 HTML（向后兼容函数）
 * @deprecated 请使用统一的 getIconHTML 函数
 */
export function getLucideIcon(name: keyof typeof lucideIcons | string): string {
  return lucideIcons[name] || ''
}
